import React from 'react'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import {
  MainWrapper,
  SectionDesc,
  ImageWrapper,
  SectionTitle,
  TypeSomething,
  SectionWrapper,
  BottomNavigation
} from '../../src/Components/SectionsComponents'
import { About, Branding, Colors, Idea, Main } from '../../src/Components/Sections/Projects/Gastrobar'
/* IMAGES */
const TShirt = '/Assets/Images/Gastrobar/TShirt.png'
const Hand = '/Assets/Images/Gastrobar/HandIcon.svg'
const Artbook = '/Assets/Images/Gastrobar/Artbook.png'
const Pattern = '/Assets/Images/Gastrobar/PatternBG.png'
const LogoGrill = '/Assets/Images/Gastrobar/LogoGrill.svg'
/*STYLES*/
import '~/Styles/Pages/Projects/gastrobar.scss'

export const Gastrobar = () => {
  return (
    <MainWrapper id="gastrobar-project-page">
      <Main/>
      <About/>
      <Idea/>
      <Colors/>
      <Branding/>

      <section id="gastrobar-identity-section">
        <SectionWrapper>
          <SectionTitle title="Identity"/>
          <Row>
            <Col xs={12} lg={6}>
              <SectionDesc/>
            </Col>
            <Col xs={12} lg={6}>
              <TypeSomething content={GastroBarAboutInfo}/>
            </Col>
          </Row>
        </SectionWrapper>
        <ImageWrapper src={Pattern} className="section-pattern"/>
      </section>
      <section id="gastrobar-symbol-section">
        <SectionWrapper className="info-wrapper">
          <SectionTitle title="Symbol"/>
          <Row style={{ display: 'flex', alignItems: 'center' }}>
            <Col xs={12} lg={6}>
              <ImageWrapper src={LogoGrill} className="symbol-logo"/>
            </Col>
            <Col xs={12} lg={6}>
              <p className="symbol-title">Green Egg Grill</p>
              <SectionDesc/>
            </Col>
          </Row>
          <ImageWrapper isDot/>
        </SectionWrapper>
        <div className="artbook-wrapper">
          {isMobileOnly && <ImageWrapper src={Hand} className="hand-icon"/>}
          <ImageWrapper src={Artbook} className="section-bg"/>
        </div>
        <div className="tshirt-wrapper">
          <SectionWrapper>
            <ImageWrapper src={TShirt} className="tshirt-image"/>
          </SectionWrapper>
          <ImageWrapper src={Pattern} className="section-pattern"/>
        </div>
        <BottomNavigation/>
      </section>
    </MainWrapper>
  )
}
